import React from "react";
import Todo from "./Todo";

function Todolist({ todos, setTodos, filteredTodos }) {
  return (
    <div className="todo-container">
      <ul className="todo-list">
        {filteredTodos.map((todo) => (
          <Todo
            todo={todo}
            key={todo.id}
            text={todo.text}
            setTodos={setTodos}
            todos={todos}
          />
        ))}
      </ul>
    </div>
  );
}

export default Todolist;
